import React, { Component }  from 'react';
import '../../App.css';
import HeroSection from '../HeroSection';
import {database} from '../../firebase';
import {storage} from '../../firebase';

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data : null,
      image: null,
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentDidMount(){
    var image = null;
    database.ref().on('value', (snapshot) => {
      storage.ref().child('images/').listAll().then(function (result){
        result.items.forEach(function(imageRef){
          imageRef.getDownloadURL().then(function(url){
            if(url !== null && image === null){
              image = url;
              console.log(url);
            }
          });
        });
      });
        this.setState({
          data: snapshot.val(),
          newData: '',
          count: snapshot.child('Count/count').val(),
          x: snapshot.child('RobotLocation/x').val(),
          y: snapshot.child('RobotLocation/y').val(),
          image: image
        });
    });
  }

  handleChange(e){
    const newData = e.target.value;
    this.setState({
      newData: newData,
    });
  }

  handleSubmit(e){
    e.preventDefault();
    database.ref().child('AMAZINGNEWDATA').set(this.state.newData); //we can use push instead of set here , database.ref() is the root!
    //database.ref("/AMAZINGNEWDATA").push(this.state.newData);
  }

  render() {
    var renderedImage;

    if(this.state.image === null){
      renderedImage = "https://firebasestorage.googleapis.com/v0/b/tdoa-23cf7.appspot.com/o/images%2Fxyplot.png?alt=media&token=835a49fa-b80b-408e-bfed-cedd43b96ee1";
    } else {
      renderedImage = this.state.image;
    }

    if(this.state.x === null){
      renderedImage = null;
    }
  
    return (
      <div className="App">
        <>
      <HeroSection />
    </>
        <div className="App--position">
          <h3> Count : {JSON.stringify(this.state.count, null,2)}</h3>
          <h3> X : {JSON.stringify(this.state.x, null,2)}</h3>
          <h3> Y : {JSON.stringify(this.state.y, null,2)}</h3>
        </div>
        <img src={renderedImage}></img>
        <p>Please refresh the page to see the updated image</p>
      </div>
    );
  }
}

export default Home;